import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";
// import GUI from "lil-gui";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader.js";
import { DRACOLoader } from "three/examples/jsm/loaders/DRACOLoader.js";

/**
 * Base
 */
// Debug
// const gui = new GUI();

// Canvas
const canvas = document.querySelector("canvas.webgl");

// Scene
const scene = new THREE.Scene();
scene.background = new THREE.Color(0xcccccc);

const dracoLoader = new DRACOLoader();
dracoLoader.setDecoderPath("/draco/");
const gltfLoader = new GLTFLoader();
gltfLoader.setDRACOLoader(dracoLoader);

let mixer = null;///////////
let mixer01 = null;
//load glb model
gltfLoader.load("models/factorylogisticanimation.glb", (gltf) => {
  gltf.scene.scale.set(0.2, 0.2, 0.2);
  // fitModelToView(gltf.scene);
  gltf.scene.position.set(0, 0, 0);
  scene.add(gltf.scene);

  mixer01 = new THREE.AnimationMixer(gltf.scene);
  const clips = gltf.animations;
  console.log(clips)
  clips.forEach((clip,index) => {
    const action = mixer01.clipAction(clip);
    action.loop = THREE.LoopRepeat;
    action.clampWhenFinished = true;
    // setting start time, push 
    const startTime = index *2 // each animation gap is 2 seconds
    action.time = startTime
    action.play();
  });
});


